var WebIM = require("../../utils/WebIM")["default"];

export default {
  data() {
    return {
      friendList: [],
      // 好友列表
      groupName: "",
      // 群名称
      groupDec: "",
      // 群简介
      allowJoin: true,
      // 是否允许任何人加入
      allowApprove: false,
      // 加入需要审批
      noAllowJoin: false,
      // 不允许任何人加入
      allowInvite: false,
      // 允许群人员邀请
      inviteFriend: [],
      // 需要加好友ID
      owner: "" // = myName

    };
  },

  components: {},
  props: {},
  onLoad: function (options) {
    this.setData({
      owner: JSON.parse(options.owner).myName
    });
  },
  onShow: function () {
    var me = this; // 获取当前用户的好友信息

    WebIM.conn.getRoster({
      success: function (roster) {
        var member = [];

        for (let i = 0; i < roster.length; i++) {
          if (roster[i].subscription == "both") {
            member.push(roster[i]);
          }
        }

        me.setData({
          friendList: member
        });
      }
    });
  },
  methods: {
    getGroupName: function (e) {
      this.setData({
        groupName: e.detail.value
      });
    },
    getGroupDec: function (e) {
      this.setData({
        groupDec: e.detail.value
      });
    },
    allowJoinFun: function (e) {
      this.setData({
        allowJoin: Boolean(e.detail.value) || false
      });
    },
    allowApproveFun: function (e) {
      this.setData({
        allowApprove: Boolean(e.detail.value) || false
      });
    },
    noAllowJoinFun: function (e) {
      this.setData({
        noAllowJoin: Boolean(e.detail.value) || false
      });
    },
    allowInviteFun: function (e) {
      this.setData({
        allowInvite: Boolean(e.detail.value) || false
      });
    },
    inviteFriendFun: function (e) {
      this.setData({
        inviteFriend: e.detail.value
      });
    },
    // 创建群组
    createGroup: function () {
      let me = this;
      let allGroups = getApp().globalData.groupList;

      if (!this.groupName.trim()) {
        wx.showModal({
          title: "请输入群名",
          confirmText: "OK",
          showCancel: false
        });
        return;
      }

      if (allGroups.reduce(function (result, v, k) {
        return result || v.name == me.groupName;
      }, false)) {
        wx.showModal({
          title: "群名被占用",
          confirmText: "OK",
          showCancel: false
        });
        return;
      }

      let options = {
        data: {
          groupname: this.groupName,
          desc: this.groupDec,
          members: this.inviteFriend,
          "public": this.allowJoin,
          // approval: this.data.allowApprove,
          // allowinvites: this.data.allowInvite,
          owner: this.owner
        },
        success: function (respData) {
          wx.showToast({
            title: "添加成功",
            duration: 2000,
            success: function (res) {
              setTimeout(() => wx.navigateTo({
                url: "../groups/groups?myName=" + me.owner
              }), 2000);
            }
          });
        },
        error: function (err) {
          wx.showToast({
            title: err.data.error_description
          });
        }
      };
      WebIM.conn.createGroupNew(options);
    },
    setData: function (obj, callback) {
      let that = this;
      let keys = [];
      let val, data;
      Object.keys(obj).forEach(function (key) {
        keys = key.split('.');
        val = obj[key];
        data = that.$data;
        keys.forEach(function (key2, index) {
          if (index + 1 == keys.length) {
            that.$set(data, key2, val);
          } else {
            if (!data[key2]) {
              that.$set(data, key2, {});
            }
          }

          data = data[key2];
        });
      });
      callback && callback();
    }
  }
};
